<script>
    import {
        getWorkshops
    } from '../../api';
    import { mapActions } from 'vuex';

    export default {
        components: {},
        props: ['propItems'],
        data() {
            return {
                data: {
                    "selected": 'underwater',
                    "full_title": '',
                    "group" : null
                },
                groups: null
            };
        },
        methods: {
            ...mapActions('modals', ['openBaseModal']),
            openModal(item) {
                this.data.data = item;
                this.data.data.full_title = item.level_title;
                this.openBaseModal(this.data);
            },
            filterData(data) {
                return data.filter(obj => {
                    return ((new Date(obj['start_date']).getTime()) > new Date().getTime())
                });
            }
        },
        created() {
            getWorkshops({type: 'adults'}).then(
                result => {
                    this.groups = this.filterData(result).slice(0, this.propItems);
                },
                error => {
                    console.warn(error)
                }
            );
        },
        mounted() {
        }
    }
</script>
