<script>
    import {
        Hooper,
        Slide
    } from 'hooper';
    import 'hooper/dist/hooper.css';
    import {
        getGroup
    } from '../../api';
    import { mapActions } from 'vuex';
    export default {
        components: {
            Hooper,
            Slide
        },
        props: ['propItems'],
        data() {
            return {
                data: {
                    selected: 'underwater',
                    full_title: '',
                    group: null
                },
                groups: null
            };
        },
        methods: {
            ...mapActions('modals', ['openBaseModal']),
            openModal(item) {
                console.log(item);
                debugger
                // TODO It would be fixed after api render end
                // this.data.data = item;
                // this.data.data.full_title = item.level_title;
                this.openBaseModal(this.data);
            },
            filterData(data) {
                return data.filter(obj => {
                    return ((new Date(obj['start_date']).getTime()) > new Date().getTime())
                });
            },
            slidePrev() {
                this.$refs['form-workshop-schedule'].slidePrev();
            },
            slideNext() {
                this.$refs['form-workshop-schedule'].slideNext();
            },
            sliderUpdated() {
                try {
                    // document.getElementById('main-slider').getElementsByClassName("hooper")[0].style.height = document.getElementById('main-slider').getElementsByClassName('hooper-slide is-active is-current')[0].getElementsByClassName("slider-item")[0].offsetHeight+"px";
                } catch (e) {
                    console.warn(e);
                }
            }
        },
        created() {
            const params = {
                course: 'lifestyle',
                type: 'adults'
            };
            getGroup(params).then(
                result => {
                    this.groups = this.filterData(result).slice(0, this.propItems);
                },
                error => {
                    console.warn(error)
                }
            );
        },
        mounted() {
        }
    }
</script>
